.background {
  composes: bg-charcoal w-100 from '@nudj/tachyons/css/tachyons.css';
}
.container {
  composes: w-100 mw8 center flex flex-column justify-center items-center pv5-ns ph0-ns pv4 ph4 white from '@nudj/tachyons/css/tachyons.css';
}
.icon {
  composes: pt4 mw5 w-100 center tc pv4 from '@nudj/tachyons/css/tachyons.css';
}
.logo {
  composes: dib from '@nudj/tachyons/css/tachyons.css';
}
.copyright {
  composes: jan-regular charcoal-tint-2 f7 db tc pt3 from '@nudj/tachyons/css/tachyons.css';
}
.links {
  composes: list flex flex-row flex-wrap justify-between items-center tc ma0 pv4-ns pa0 from '@nudj/tachyons/css/tachyons.css';
}
.link {
  composes: justify-center items-center w-25-l w-50-m w-100 tc ph1 pv1-ns pv3 from '@nudj/tachyons/css/tachyons.css';
}
.release,
.roadmap,
.hiring,
.terms {
  composes: jan-bold relative f3-ns f4 link no-underline dib-ns di mh1 mt1-ns mb3-ns mv2 white dim from '@nudj/tachyons/css/tachyons.css';
}
.release:after,
.roadmap:after,
.hiring:after,
.terms:after {
  background-image: url('/assets/images/table-line-1.svg');
  background-repeat: no-repeat;
  bottom: -10px;
  content: '';
  height: 10px;
  left: 0;
  position: absolute;
  width: 100%;
}
.release:after {
  background-position: left center;
}
.roadmap:after {
  background-position: 33% center;
}
.hiring:after {
  background-position: 66% center;
}
.terms:after {
  background-position: right center;
}

.label {
  composes: jan-thin tc pt2 mh0 mv0 dn dib-ns from '@nudj/tachyons/css/tachyons.css';
}



